import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {Wallet} from "../../providers/model/wallet";
import {TransactionService} from "../../providers/transaction.service";


@IonicPage()
@Component({
  selector: 'page-wallet-detail',
  templateUrl: 'wallet-detail.html'
})
export class WalletDetailPage {
  wallet: Wallet;
  walletTransaction;
  days = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];
  months = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"];

  currencies= Object.keys;

  constructor( public navCtrl: NavController,
               navParams: NavParams,
               private transactionService: TransactionService) {
    this.wallet = navParams.get('wallet');
    this.walletTransaction = this.transactionService.getWalletTransaction(this.wallet.trigram, this.wallet.address);
  }

  ionViewDidLoad() {

  }
}



